import { useEffect, useRef, useImperativeHandle, forwardRef, useState } from 'react'
import { Entity, Scene } from 'aframe-react'

interface ARSceneAFrameProps {
  className?: string
  onSceneReady?: () => void
  enableAR?: boolean
}

export interface ARSceneAFrameRef {
  addEntity: (data: any) => string
  removeEntity: (id: string) => void
  getScene: () => any
  captureScreenshot: () => string
}

export const ARSceneAFrame = forwardRef<ARSceneAFrameRef, ARSceneAFrameProps>(({ 
  className = '',
  onSceneReady,
  enableAR = true
}, ref) => {
  const sceneRef = useRef<any>(null)
  const [entities, setEntities] = useState<Array<{ id: string; data: any }>>([])
  const [sceneReady, setSceneReady] = useState(false)

  useEffect(() => {
    let cleanup: (() => void) | undefined
    // Aguardar o A-Frame estar pronto
    if (typeof window !== 'undefined' && (window as any).AFRAME) {
      setSceneReady(true)
      if (onSceneReady) {
        onSceneReady()
      }

      // Apenas esconder o bot√£o de VR uma √∫nica vez (sem c√°lculos/intervalos)
      requestAnimationFrame(() => {
        const vrButton = document.querySelector('.a-enter-vr-button') as HTMLElement | null
        if (vrButton) {
          vrButton.style.display = 'none'
          vrButton.style.visibility = 'hidden'
          vrButton.style.pointerEvents = 'none'
        }
      })

      // Blindar body/html contra estilos inline agressivos de libs
      const sanitizeViewportContainers = () => {
        const htmlEl = document.documentElement
        const bodyEl = document.body
        if (htmlEl) {
          htmlEl.style.setProperty('margin-left', '0px', 'important')
          htmlEl.style.setProperty('margin-top', '0px', 'important')
          htmlEl.style.setProperty('width', 'auto', 'important')
          htmlEl.style.setProperty('height', 'auto', 'important')
          htmlEl.style.setProperty('transform', 'none', 'important')
          htmlEl.style.setProperty('left', '0px', 'important')
          htmlEl.style.setProperty('top', '0px', 'important')
          htmlEl.style.setProperty('position', 'static', 'important')
        }
        if (bodyEl) {
          bodyEl.style.setProperty('margin-left', '0px', 'important')
          bodyEl.style.setProperty('margin-top', '0px', 'important')
          bodyEl.style.setProperty('left', '0px', 'important')
          bodyEl.style.setProperty('top', '0px', 'important')
          bodyEl.style.setProperty('position', 'static', 'important')
          bodyEl.style.setProperty('width', 'auto', 'important')
          bodyEl.style.setProperty('height', 'auto', 'important')
          bodyEl.style.setProperty('transform', 'none', 'important')
          bodyEl.style.setProperty('overflow', 'hidden', 'important')
        }
      }

      sanitizeViewportContainers()

      const observer = new MutationObserver(() => sanitizeViewportContainers())
      observer.observe(document.documentElement, { attributes: true, attributeFilter: ['style'] })
      observer.observe(document.body, { attributes: true, attributeFilter: ['style'] })

      cleanup = () => {
        observer.disconnect()
      }
    }
    return () => {
      if (cleanup) cleanup()
    }
  }, [onSceneReady])

  useImperativeHandle(ref, () => ({
    addEntity: (data: any) => {
      const id = `entity-${Date.now()}`
      setEntities(prev => [...prev, { id, data }])
      return id
    },
    removeEntity: (id: string) => {
      setEntities(prev => prev.filter(e => e.id !== id))
    },
    getScene: () => {
      if (typeof window !== 'undefined' && (window as any).AFRAME) {
        const sceneEl = document.querySelector('a-scene')
        return sceneEl || sceneRef.current
      }
      return sceneRef.current
    },
    captureScreenshot: () => {
      if (typeof window !== 'undefined' && (window as any).AFRAME) {
        const sceneEl = document.querySelector('a-scene')
        if (sceneEl) {
          const renderer = (sceneEl as any).renderer
          if (renderer && renderer.domElement) {
            return renderer.domElement.toDataURL('image/png')
          }
        }
      }
      return ''
    }
  }), [])

  // Props padr√£o para a cena AR - SEMPRE embedded e SEM VR
  const sceneProps: any = {
    embedded: true,
    vrModeUI: 'enabled: false',
    deviceOrientationPermissionUI: 'enabled: true',
    renderer: 'alpha: true; antialias: true; colorManagement: true; physicallyCorrectLights: false; sortObjects: true; logarithmicDepthBuffer: false; precision: mediump;',
    arjs: enableAR ? 'sourceType: webcam; videoTexture: true; debugUIEnabled: false;' : ''
  }

  // N√£o precisamos adicionar aqui pois j√° est√° no sceneProps acima

  return (
    <div className={`ar-scene-aframe ${className}`} style={{ width: '100%', height: '100%', position: 'relative' }}>
      {sceneReady && (
        <Scene {...sceneProps}>
          {/* C√¢mera com controles habilitados */}
          <Entity
            id="camera"
            camera={{ active: true }}
            lookControls={{
              enabled: true,
              reverseMouseDrag: false,
              touchEnabled: true,
              magicWindowTrackingEnabled: true
            }}
            wasdControls={{
              enabled: true,
              acceleration: 20,
              fly: false,
              controls: 'gamepad keyboard'
            }}
            position="0 1.6 0"
          />

          {/* Luz ambiente */}
          <Entity light={{ type: 'ambient', color: '#888' }} />

          {/* Luz direcional */}
          <Entity
            light={{
              type: 'directional',
              color: '#fff',
              intensity: 0.6
            }}
            position="1 1 1"
          />

          {/* Entidades din√¢micas */}
          {entities.map(entity => (
            <Entity
              key={entity.id}
              {...entity.data}
            />
          ))}

          {/* Exemplo: Cubo b√°sico */}
          <Entity
            id="default-cube"
            geometry={{ primitive: 'box', width: 0.6, height: 0.6, depth: 0.6 }}
            material={{ color: '#4CC3D9' }}
            position="0 1 -1.5"
            animation={{
              property: 'rotation',
              to: '0 360 0',
              loop: true,
              dur: 8000
            }}
          />
        </Scene>
      )}
      {!sceneReady && (
        <div style={{ 
          display: 'flex', 
          alignItems: 'center', 
          justifyContent: 'center', 
          height: '100%',
          color: '#fff'
        }}>
          Carregando A-Frame...
        </div>
      )}
    </div>
  )
})

ARSceneAFrame.displayName = 'ARSceneAFrame'

